	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<p>One of the classic problems our operating system must handle is communication between threads.\nWe define a producer thread as one that creates or produces data.\nA consumer thread is a thread that consumes (and removes) data.\nThe communication mechanism we will use in this chapter is a mailbox (Figure 2.6).\nThe mailbox has a Data field and a Status field.\nMailboxes will be statically allocated global structures.\nBecause they are global variables, it means they will exist permanently and can be carefully shared by more than one task.\nThe advantage of using a structure like a mailbox for a data flow problem is that we can decouple the producer and consumer threads. </p>","frontmatter":{"title":"Inter-thread communication in C","author":"Maximiliano Valencia","date":"23 March, 2018"}}},"pathContext":{"slug":"/posts/inter_thread_communication/"}}

